import React, { useState, useEffect } from 'react'
import { Row, Col, Modal, ModalHeader, ModalBody, ModalFooter, Button, Table } from "reactstrap";
import numberWithCommas from "reactnumberwithcommas";

const ModalPembelianDetail = (props) => {
    const clientID = props.clientID
    const token = props.token
    const detail = props.detailUser || {}
    const detailPembelian = props.detailPembelian || []
    const [listBarang, setListBarang] = useState([])
    const [totalBayar, setTotalBayar] = useState(0)

    useEffect(() => {
        console.log(detailPembelian, 'detailPembelian')
        setListBarang(detailPembelian.item)
        setTotalBayar(detailPembelian.totalTagihan)
        return () => {

        }
    }, [clientID, token, detailPembelian])
    const handleClose = () => {
        props.setOpen(false)
    }

    return (
        <Modal
            isOpen={props.open}
            toggle={handleClose}
            wrapClassName="modal-right"
        >
            <ModalHeader toggle={handleClose}>List barang<br /><b>{detail.nama}</b></ModalHeader>
            <ModalBody>
                <Row>
                    <Col>
                        <Table responsive size="sm" hover>
                            <thead>
                                <tr>
                                    <th>Produk</th>
                                    <th>Harga</th>
                                    <th>Qty</th>
                                    <th>Jumlah</th>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    listBarang && (
                                        listBarang.map((el, i) => {
                                            return (
                                                <tr key={i}>
                                                    <th>{el ? el.namaProduk : ''}</th>
                                                    <th>{el ? el.harga : ''}</th>
                                                    <td>{el ? el.qty : ''}</td>
                                                    <td>{el ? (el.qty * el.harga) : ''}</td>
                                                </tr>
                                            )
                                        })
                                    )
                                }
                            </tbody>

                        </Table>
                    </Col>
                    <Col xs={12} md={12} className="marginFrom">
                        <h4>Total : {numberWithCommas(totalBayar)}</h4>
                    </Col>
                    {detailPembelian.isReturn === true && <Col xs={12} md={12} className="marginFrom">
                        <h4>Alasan : {detailPembelian.alasan}</h4>
                    </Col>}
                </Row>


            </ModalBody>
            <ModalFooter>
                <Button color="secondary" onClick={handleClose}>Tutup</Button>
            </ModalFooter>
        </Modal>
    )
}

export default ModalPembelianDetail;