import React, { useState } from 'react'
import { Row, Col, Modal, ModalHeader, ModalBody, ModalFooter, Button } from "reactstrap";
import mPembelian from '../../../../Controler/c_Pembelian'
import Notifikasi from '../../../../helpers/Notifikasi'
import { isArray } from "util";
import { TextField } from '@material-ui/core';

const ModalPembelianReturn = (props) => {
    // console.log(props, 'props')
    const clientID = props.clientID
    const token = props.token
    const detailPembelian = props.detailPembelian || []
    const klinik = props.klinik
    const [alasan, setAlasan] = useState('')

    const handleClose = () => {
        props.setOpen(false)
    }

    const handleSave = () => {

        mPembelian.returnPembelian(clientID, token, klinik.kodeklinik, detailPembelian.notrans , alasan).then(resp => {
            if (resp) {
                Notifikasi("success", "Berhasil return penjualan", "")
                props.setIsReload(true)
                handleClose()
            } else {
                Notifikasi("error", "Server tidak dapat menjalankan perintah Anda saat ini. Silakan coba sesaat lagi", "Gagal Return Pembelian")
            }
        }).catch(err => {
            if (isArray(err) && err.length > 0) {
                Notifikasi("error", err[0].message, "Gagal Return Pembelian")
            } else if (typeof err) {
                Notifikasi("error", err.message, "Gagal Return Pembelian")
            } else {
                console.log(err);
            }
        })

        localStorage.removeItem('listBarang')
        
    }

    return (
        <Modal
            isOpen={props.open}
            toggle={handleClose}
            wrapClassName="modal-right"
        >
            <ModalHeader toggle={handleClose}>Return Pembelian<br /></ModalHeader>
            <ModalBody>
                <Row>
                    <Col xs={12} md={12} className="marginFrom">
                        <TextField
                            id="alasan"
                            name="alasan"
                            label="Alasan"
                            fullWidth
                            value={alasan}
                            onChange={(ev) => setAlasan(ev.target.value)}
                            variant="outlined"
                            type="text"
                        />
                    </Col>
                </Row>
            </ModalBody>
            <ModalFooter>
                <Button color="secondary" onClick={handleClose}>Cancel</Button>
                <Button color="primary" onClick={handleSave}>Return Pembelian</Button>
            </ModalFooter>
        </Modal>
    )
}

export default ModalPembelianReturn;