import React, { useState, useEffect } from 'react'
import { Row, Col, Modal, ModalHeader, ModalBody, ModalFooter, Button, Table } from "reactstrap";
import mPembelian from '../../../../Controler/c_Pembelian'
import Notifikasi from '../../../../helpers/Notifikasi'
import { isArray } from "util";
import SettingsIcon from '@material-ui/icons/Settings';
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';
import Tooltip from '@material-ui/core/Tooltip';
import AddCircleIcon from '@material-ui/icons/AddCircle';
import ButtonMI from '@material-ui/core/Button';
import CancelIcon from '@material-ui/icons/Cancel';
import Confirm from '../../../../helpers/Confirm'
import { TextField } from '@material-ui/core';
import numberWithCommas from "reactnumberwithcommas";
import Autocomplete from '@material-ui/lab/Autocomplete';

const ModalPembelian = (props) => {
    // console.log(props, 'props')
    const clientID = props.clientID
    const token = props.token
    const detail = props.detailUser || {}
    const [isList, setIsList] = useState(true)
    const [isReload, setIsReload] = useState(true)
    const [isConfirm, setIsConfirm] = useState(false)
    const [textConfirm, setTextConfirm] = useState("")
    const [detailProduk, setDetailProduk] = useState({})
    const [produk, setProduk] = useState({})
    const klinik = props.klinik
    const [listBarang, setListBarang] = useState([])
    const [totalBayar, setTotalBayar] = useState(0)
    const [listProduk, setlistProduk] = useState([])
    const [form, setForm] = useState({
        kodeProduk: '',
        namaProduk: '',
        jenis: '',
        kategori: '',
        harga: 0,
        satuan:''
    })


    useEffect(() => {

        mPembelian.listProduk(clientID, token, klinik.kodeklinik).then(resp => {
            setlistProduk(resp);
        }).catch(err => {
            console.error(err);
        }).finally(() => setIsReload(false))

        // console.log(listProduk, 'listProduk')

        return () => {

        }
    }, [clientID, token, klinik.kodeklinik, isReload, detail.qty])
    const handleClose = () => {
        props.setOpen(false)
    }

    const handleSimpan = () => {
    //    console.log(listProduk, 'listProduk')
       var a = [];
       var data ={
            'kodeProduk' : produk.kodeProduk,
            'namaProduk' : produk.namaProduk,
            'qty' : form.qty,
            'satuan' : form.satuan,
            'harga' : form.harga,
            'total' : (form.qty*form.harga)
       }

       a = JSON.parse(localStorage.getItem('listBarangBeli')) || [];
       a.push(data);
       localStorage.setItem('listBarangBeli', JSON.stringify(a));

       setListBarang(JSON.parse(localStorage.getItem('listBarangBeli')))
       setIsList(true)

       var total = JSON.parse(localStorage.getItem('listBarangBeli'));

       // get sum of msgCount prop across all objects in array
        var msgTotal = total.reduce(function(prev, cur) {
            return prev + cur.total;
        }, 0);
        
        // console.log(msgTotal, 'msgTotal');
       setTotalBayar(msgTotal)
    }


    const handleSave = () => {

        mPembelian.simpanPembelian(clientID, token, klinik.kodeklinik, listBarang, totalBayar).then(resp => {
            if (resp) {
                Notifikasi("success", "Berhasil disimpan", "")
                props.setIsReload(true)
                handleClose()
            } else {
                Notifikasi("error", "Server tidak dapat menjalankan perintah Anda saat ini. Silakan coba sesaat lagi", "Gagal Simpan")
            }
        }).catch(err => {
            if (isArray(err) && err.length > 0) {
                Notifikasi("error", err[0].message, "Gagal Simpan")
            } else if (typeof err) {
                Notifikasi("error", err.message, "Gagal Simpan")
            } else {
                console.log(err);
            }
        })

        localStorage.removeItem('listBarangBeli')
    //    console.log(qty, 'qty')
    // console.log(listBarang, 'listBarangBeli')
        
    }

    const add = () => {
        setIsList(false)
    }
    const edit = (data) => {
        // console.log(data, 'data')
    }

    const confirmHapus = (data) => {
        setDetailProduk(data)
        setTextConfirm(`Yakin ingin hapus jadwal ${data.namaProduk}? Data tidak dapat dikembalikan setelah hapus`)
        setIsConfirm(true)
    }

    const hapus = () => {
        // console.log(detailProduk.kodeProduk, 'detailProduk.kodeProduk')

        let listBarangBeli = JSON.parse(localStorage.getItem("listBarangBeli")); // updated
        let barangBaru = listBarangBeli.filter(e => e.kodeProduk !== detailProduk.kodeProduk)


        localStorage.setItem('listBarangBeli', JSON.stringify(barangBaru));

        setListBarang(JSON.parse(localStorage.getItem('listBarangBeli')))

       var total = JSON.parse(localStorage.getItem('listBarangBeli'));

       // get sum of msgCount prop across all objects in array
        var msgTotal = total.reduce(function(prev, cur) {
            return prev + cur.total;
        }, 0);
        
        // console.log(msgTotal, 'msgTotal');
       setTotalBayar(msgTotal)


        setIsConfirm(false)


        // for (var i =0; i< listBarang.length; i++) {
        //     let barang = JSON.parse(listBarang[i]);
        //     if (barang.kodeProduk == produk.kodeProduk) {
        //         barang.splice(i, 1);
        //     }
        // }
        // let barangBaru = JSON.stringify(listBarang); //Restoring object left into listBarang again
        // localStorage.setItem("listBarangBeli", barangBaru);
    }


    const handleChange = (ev) => {
        var value = ev.target.value;
        const key = ev.target.id || ev.target.name;
        var newForm = { ...form }
        newForm[key] = value;
        // console.log(newForm, 'newForm')
        setForm(newForm);
    }


    return (
        <Modal
            isOpen={props.open}
            toggle={handleClose}
            wrapClassName="modal-right"
        >
            <ModalHeader toggle={handleClose}>List barang<br /><b>{detail.nama}</b></ModalHeader>
            <ModalBody>
                {isList ? (
                    <Row>
                        <Col>

                            <ButtonMI
                                onClick={add}
                                size="small"
                                variant="contained"
                                color="primary"
                                startIcon={<AddCircleIcon />}
                            >Add Barang</ButtonMI>
                            <Table responsive size="sm" hover>
                                <thead>
                                    <tr>
                                        <th>Produk</th>
                                        <th>Harga</th>
                                        <th>Qty</th>
                                        <th>Jumlah</th>
                                        <th className="text-center"><SettingsIcon /></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {
                                        listBarang && (
                                            listBarang.map((el, i) => {
                                                return (
                                                    <tr key={i}>
                                                        <th>{el ? el.namaProduk : ''}</th>
                                                        <th>{el ? el.harga : ''}</th>
                                                        <td>{el ? el.qty : ''}</td>
                                                        <td>{el ? el.total : ''}</td>
                                                        <td width="60">
                                                            <Tooltip onClick={edit.bind(this, el)} className="klik" title="edit"><EditIcon /></Tooltip>
                                                            <Tooltip onClick={confirmHapus.bind(this, el)} className="klik" title="delete"><DeleteIcon /></Tooltip>
                                                        </td>
                                                    </tr>
                                                )
                                            })
                                        )
                                    }
                                </tbody>

                            </Table>
                        </Col>
                            <Col xs={12} md={12} className="marginFrom">
                            <h4>Total : {numberWithCommas(totalBayar)}</h4>
                            </Col>
                    </Row>
                ) : (
                        <Row>
                            <Col>
                                <ButtonMI
                                    onClick={() => setIsList(true)}
                                    size="small"
                                    variant="contained"
                                    color="secondary"
                                    startIcon={<CancelIcon />}
                                >Close Form</ButtonMI>
                            </Col>
                            <Col xs={12} md={12} className="marginFrom">
                                <Autocomplete
                                    style={{ marginTop: 15 }}
                                    id="combo-box-demo"
                                    options={listProduk}
                                    getOptionLabel={(option) => option.namaProduk}
                                    fullWidth
                                    value={produk}
                                    onChange={(event, values) => setProduk(values)}
                                    renderInput={(params) => <TextField {...params} label="Pilih produk" variant="outlined" />}
                                />
                            </Col>
                            <Col xs={12} md={12} className="marginFrom">
                                <TextField
                                    id="harga"
                                    name="harga"
                                    label="Harga"
                                    fullWidth
                                    value={form.harga}
                                    onChange={handleChange}
                                    variant="outlined"
                                    type="number"
                                />
                            </Col>
                            <Col xs={12} md={12} className="marginFrom">
                                <TextField
                                    id="qty"
                                    name="qty"
                                    label="Qty"
                                    fullWidth
                                    value={form.qty}
                                    onChange={handleChange}
                                    variant="outlined"
                                />
                            </Col>
                            <Col xs={12} md={12} className="marginFrom">
                                <h4>Satuan : {produk.satuan}</h4>
                            </Col>



                        </Row>
                    )}


            </ModalBody>
            <ModalFooter>
                <Button color="secondary" onClick={handleClose}>Cancel</Button>
                {isList ? <Button color="primary" onClick={handleSave}>Save Pembelian</Button> : <Button color="primary" onClick={handleSimpan}>Save Barang</Button>}
            </ModalFooter>

            {isConfirm && (
                <Confirm open={isConfirm} setOpen={setIsConfirm} textConfirm={textConfirm} oke={hapus} />
            )}
        </Modal>
    )
}

export default ModalPembelian;