import React, { Fragment, useEffect, useState } from "react"
import { Row, Button, Col, Card, CardBody, Breadcrumb, BreadcrumbItem } from "reactstrap"
import { Colxx, Separator } from "../../../../components/common/CustomBootstrap"
import { ContextMenuTrigger } from "react-contextmenu"
import mUser from '../../../../Controler/c_User'
import mPembelian from '../../../../Controler/c_Pembelian'
import Skeleton from 'react-loading-skeleton'
import ModalPembelian from './Modal.pembelian'
import ModalPembelianDetail from './Modal.detail'
import ModalPembelianReturn from './Modal.return'
import Confirm from '../../../../helpers/Confirm'
import IntlMessages from "../../../../helpers/IntlMessages"
import DataTablePagination from "../../../../components/DatatablePagination"
import ReactTable from "react-table"
import { MuiPickersUtilsProvider, DatePicker } from '@material-ui/pickers';
import DateFnsUtils from '@date-io/date-fns';
import moment from 'moment-timezone'

const Admin = (props) => {
    const authLogin = JSON.parse(localStorage.getItem('keminaAdmin'))
    const [isReload, setIsReload] = useState(true)
    const [listPembelian, setListPembelian] = useState([])
    const [user, setUser] = useState({})
    const [detailPembelian, setDetailPembelian] = useState({})
    const [returnPembelian, setReturnPembelian] = useState({})
    const [isConfirm, setIsConfirm] = useState(false)
    const [isModal, setIsModal] = useState(false)
    const [isModalDetail, setIsModalDetail] = useState(false)
    const [isModalReturn, setIsModalReturn] = useState(false)
    const [tgldari, setTgldari] = useState(moment())
    const [tglSampai, setTglSampai] = useState(moment())


    useEffect(() => {
        mPembelian.listPembelian(authLogin.clientID, authLogin.token, authLogin.klinik.kodeklinik, moment(tgldari).format("YYYY-MM-DD"), moment(tglSampai).format("YYYY-MM-DD")).then(resp => {
            setListPembelian(resp);
        }).catch(err => {
            console.error(err);
        }).finally(() => setIsReload(false));
        return () => { };
    }, [authLogin.clientID, authLogin.token, authLogin.username, authLogin.klinik.kodeklinik, isReload, tgldari, tglSampai]);
    useEffect(() => {
        mUser.dataUser(authLogin.clientID, authLogin.token, authLogin.username, authLogin.jenisUser).then(resp => {
            setUser(resp);
        }).catch(err => {
            console.error(err);
        })
        return () => {

        }
    }, [authLogin.clientID, authLogin.token, authLogin.username, authLogin.jenisUser])

 
    const handleAdd = () => {
        setDetailPembelian({})
        setIsModal(true)
    }
    const handleReturn = (data) => {
        setReturnPembelian(data.original)
        setIsModalReturn(true)
    }
    const confirmDetail = (data) => {
        setDetailPembelian(data.original)
        setIsModalDetail(true)
    }
    const handleDetail = () => {
        setIsModal(true)
    }
    const dataTableColumns = [
        {
            Header: "No Transaksi",
            accessor: "notrans",
            Cell: props => <p className="list-item-heading">{isReload ? <Skeleton /> : props.value}</p>
        },
        {
            Header: "Tanggal",
            accessor: "tanggal",
            Cell: props => <p className="list-item-heading">{isReload ? <Skeleton /> : props.value}</p>
        },
        {
            Header: "Total Tagihan",
            accessor: "totalTagihan",
            Cell: props => <p className="list-item-heading">{isReload ? <Skeleton /> : props.value}</p>
        },
        {
            Header: <p className="text-center">Aksi</p>,
            Cell: props => isReload ? <Skeleton /> : <p className="text-muted">
                {props.original.isReturn !== true && <Button style={{ marginRight: 3 }} color="warning" onClick={handleReturn.bind(this, props)}>Return</Button>} 
                <Button color="danger" onClick={confirmDetail.bind(this, props)}>Detail</Button>
            </p>
        }
    ];
    if (user.jenisUser === "admin") {
        return (
            <Fragment>
                <Row>
                    <Colxx xxs="12">
                        <div className="mb-2">
                            <h1>
                                Transaksi
                            </h1>
                            <Breadcrumb className="pt-0 breadcrumb-container d-none d-sm-block d-lg-inline-block">
                                <BreadcrumbItem>
                                    <IntlMessages id="menu.pembelian" />
                                </BreadcrumbItem>
                            </Breadcrumb>

                            <div className="text-zero top-right-button-container">
                               
                                    <Button
                                        color="primary"
                                        size="lg"
                                        className="top-right-button"
                                        onClick={handleAdd}
                                    >
                                        Add  <IntlMessages id="menu.pembelian" />
                                    </Button>
                              
                            </div>
                        </div>

                        <Separator className="mb-5" />
                    </Colxx>
                </Row>
                <Row>
                <Col xs={6}>
                    <MuiPickersUtilsProvider utils={DateFnsUtils}>
                        <DatePicker
                            fullWidth={true}
                            label="Tanggal Dari"
                            format="dd-MM-yyyy"
                            value={tgldari}
                            onChange={(d) => setTgldari(d)}
                            ampm={false}
                        />
                    </MuiPickersUtilsProvider>
                </Col>
                <Col xs={6}>
                    <MuiPickersUtilsProvider utils={DateFnsUtils}>
                        <DatePicker
                            fullWidth={true}
                            label="Tanggal Sampai"
                            format="dd-MM-yyyy"
                            value={tglSampai}
                            onChange={(d) => setTglSampai(d)}
                            ampm={false}
                        />
                    </MuiPickersUtilsProvider>
                </Col>
                    <Colxx xxs="12" className="mb-4 jarakAtas">
                        <ContextMenuTrigger id="menu_id" >
                            <Card>
                                <CardBody>
                                    <ReactTable
                                        data={listPembelian}
                                        columns={dataTableColumns}
                                        defaultPageSize={5}
                                        filterable={false}
                                        showPageJump={true}
                                        PaginationComponent={DataTablePagination}
                                        showPageSizeOptions={true}
                                    />
                                </CardBody>
                            </Card>
                        </ContextMenuTrigger>
                    </Colxx>
                </Row>

                {isModal && (
                    <ModalPembelian open={isModal} setOpen={setIsModal} clientID={authLogin.clientID} token={authLogin.token} setIsReload={setIsReload} detailPembelian={detailPembelian} klinik={authLogin.klinik} />
                )}


                {isModalDetail && (
                    <ModalPembelianDetail open={isModalDetail} setOpen={setIsModalDetail} clientID={authLogin.clientID} token={authLogin.token} setIsReload={setIsReload} detailPembelian={detailPembelian} klinik={authLogin.klinik} />
                )}


                {isModalReturn && (
                    <ModalPembelianReturn open={isModalReturn} setOpen={setIsModalReturn} clientID={authLogin.clientID} token={authLogin.token} setIsReload={setIsReload} detailPembelian={returnPembelian} klinik={authLogin.klinik} />
                )}

                {isConfirm && (
                    <Confirm open={isConfirm} setOpen={setIsConfirm} oke={handleDetail} />
                )}
            </Fragment>
        )
    } else {
        return (
            <p>You do not have access rights</p>
        )
    }
}
export default Admin;