import GQL from '../helpers/GQL'


const Pembelian = {
    listPembelian: (clientID, token, kodeklinik, tglDari, tglSampai) => {
        return new Promise((resolve, reject) => {
            const query = `
            query{
                listPembelian(kodeklinik:"kode123", tglDari:"${tglDari}", tglSampai:"${tglSampai}"){
                  _id
                  tanggal
                  notrans
                  totalTagihan
                  isReturn
                  alasan
                  item{
                      kodeProduk
                      namaProduk
                      harga
                      qty
                      satuan
                  }
                }
              }
            `
            GQL(query, clientID, token).then(resp => {
                const listDatas = []
                resp.data.listPembelian.map(el => {
                    // console.log(el)
                    if (el.kategori) {
                        var kategori = el.kategori.kategoriProduk
                        var kodekategori = el.kategori.kodeKategoriProduk
                    }

                    if (el.jenis) {
                        var jenis = el.jenis.jenisProduk
                        var kodejenis = el.jenis.kodeJenisProduk
                    }

                    return listDatas.push({
                        ...el,
                        kodekategori,
                        kategori,
                        kodejenis,
                        jenis,
                    })
                })
                if (resp.errors) return (reject(resp.errors));;
                if (resp.data && resp.data.listPembelian) return (resolve(listDatas));
                return (resolve([]))
            }).catch(err => {
                return (reject(err));
            })
        })
    },
    simpanPembelian: (clientID, token, kodeklinik, listBarang, totalBayar) => {
        return new Promise((resolve, reject) => {
            // console.log(listBarang, 'listBarang')
            let input = '[';
            for (var i = 0, j = listBarang.length; i < j; i++) {
                const { kodeProduk, namaProduk, satuan, qty, harga } = listBarang[i];
                if (i > 0) input += `,`;
                var _kodeProduk = kodeProduk ? `,kodeProduk:"${kodeProduk}"` : '';
                input += `{namaProduk:"${namaProduk}",qty:${qty},harga:${harga},satuan:"${satuan}"${_kodeProduk}}`;
            };
            input += ']';
            const query = `
            mutation{
                simpanPembelian(kodeklinik: "${kodeklinik}",totalTagihan:${totalBayar},input:${input})
              }
        `
            GQL(query, clientID, token).then(resp => {
                if (resp.errors) return (reject(resp.errors));;
                if (resp.data && resp.data.simpanKlinik) return (resolve(resp.data.simpanKlinik));
                return (resolve([]))
            }).catch(err => {
                return (reject(err));
            })
        })
    },
    returnPembelian: (clientID, token, kodeklinik, noTrans, alasan) => {
        return new Promise((resolve, reject) => {
            const query = `
            mutation{
                returnPembelian(kodeklinik: "${kodeklinik}",notrans: "${noTrans}", alasan:"${alasan}")
              }
            `
            GQL(query, clientID, token).then(resp => {
                if (resp.errors) return (reject(resp.errors));
                if (resp.data && resp.data.returnPembelian) return (resolve(true));
                return (resolve([]))
            }).catch(err => {
                return (reject(err));
            })
        })
    },
    loadSelecBox: (clientID, token, kodeklinik) => {
        return new Promise((resolve, reject) => {
            const query = `
                query {
                    listKategoriProduk(kodeklinik:"${kodeklinik}"){
                        kodeKategoriProduk
                        kategoriProduk
                      }
                      listJenisProduk(kodeklinik:"${kodeklinik}"){
                        kodeJenisProduk
                        jenisProduk
                      }
                }
            `
            GQL(query, clientID, token).then(resp => {
                if (resp.errors) return (reject(resp.errors));;
                const kategori = resp.data.listKategoriProduk
                const jenis = resp.data.listJenisProduk
                if (resp.data && resp.data.listKategoriProduk) return (resolve({ kategori, jenis }));
                return (resolve([]))
            }).catch(err => {
                return (reject(err));
            })
        })
    },
    listProduk: (clientID, token, kodeklinik) => {
        return new Promise((resolve, reject) => {
            const query = `
                query {
                    listProduk(kodeklinik:"${kodeklinik}"){
                        kodeklinik,
                        kodeProduk,
                        namaProduk,
                        satuan
                        harga,
                        kategori{
                            kodeKategoriProduk,
                            kategoriProduk
                        },
                        jenis{
                            kodeJenisProduk,
                            jenisProduk,
                        }
                      }
                }
            `
            GQL(query, clientID, token).then(resp => {
                const listDatas = []
                resp.data.listProduk.map(el => {
                    // console.log(el)
                    if (el.kategori) {
                        var kategori = el.kategori.kategoriProduk
                        var kodekategori = el.kategori.kodeKategoriProduk
                    }

                    if (el.jenis) {
                        var jenis = el.jenis.jenisProduk
                        var kodejenis = el.jenis.kodeJenisProduk
                    }

                    return listDatas.push({
                        ...el,
                        kodekategori,
                        kategori,
                        kodejenis,
                        jenis,
                    })
                })
                if (resp.errors) return (reject(resp.errors));;
                if (resp.data && resp.data.listProduk) return (resolve(listDatas));
                return (resolve([]))
            }).catch(err => {
                return (reject(err));
            })
        })
    },
    updatePembelian: (clientID, token, kodeklinik, notrans, namaProduk, satuan, kode, qty) => {
        return new Promise((resolve, reject) => {
            const query = `
            mutation{
                simpanPembelian(
                    kodeklinik: "${kodeklinik}",
                    notrans: "${notrans}",
                    input:{
                        kodeProduk:"${kode}", 
                        namaProduk: "${namaProduk}", 
                        qty: ${qty}, 
                        satuan:"${satuan}"
                    }
                )
              }
        `
            GQL(query, clientID, token).then(resp => {
                if (resp.errors) return (reject(resp.errors));;
                if (resp.data && resp.data.simpanKlinik) return (resolve(resp.data.simpanKlinik));
                return (resolve([]))
            }).catch(err => {
                return (reject(err));
            })
        })
    },
};

export default Pembelian;
